.cardlist-container{
    align-content: center;
   align-items: center;
  
      display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 50px;}
  
    @media screen and (max-width: 900px) {
      .cardlist-container{
        grid-template-columns:auto;
    
      }
    }

    .card-container{
      border-radius: 4px;
      padding-bottom: 10px;
      /*background-color: gainsboro ;*/
      box-shadow:6px 0px 12px 4px lightgray ;
      }
      .name{
          color:white;
          font-size: 20px;
          text-decoration: none;
      
      }
      
      
      .project-image{
          height: 150px;
          width: 150px;
              
      }
      
      
      
      @media screen and (max-width: 900px) {
         .card-container{
          box-shadow:4px 0px 8px 2px lightgray ;
         
         }
          .project-image{ height:60px;
          width: 60px;
      }
      .name{
          text-decoration: underline;
          font-size:15px;
      }
      }
      